import axios from "axios"

export const home = {
    state: () => ({ 
        sale:[],
        BASIC_URL: 'https://wt.altech.uz',
     }),
    mutations: { 
        setSale(state,payload){
            state.sale = payload
        }
    },
    actions: { 
        setSale(context){
            axios.get(`${context.state.BASIC_URL}/product/sale`)
            .then(res => {
                console.log(res.data)
                context.commit('setSale',res.data)
            })
        }
    },
    getters: { 
        getSale(state){
            return state.sale
        }
    }
}