<template>
    <div class="home ">
        <div class="home__top">
            <div class="container">
            <section class="home__slider">
                <div class="home__left" v-if="sale.length>0">
                    <div class="home__title">Супер распродажа!</div>
                    <Splide class="splide" :options=" {
                    type: 'loop', 
                    rewind: true,
                    arrows:false,
                    pagination:false, 
                    cover: false, 
                    autoplay: true, 
                    interval:4000,
                    paginationKeyboard:false,
                    keyboard:false,
                    classes: {
                        arrow : 'splide__arrow arrow',
                        pagination: 'splide__pagination pagination',
                        page: 'splide__pagination__page pagination__item' },
                    breakpoints: {
                        475: {
                            arrows: false
                        },
                    }
                    }">
                        <SplideSlide v-for="item of sale"  :key="item._id" class="home__slide">
                            <oneproductVue :item="item"/>
                        </SplideSlide>
                    </Splide>
                </div>
                <Splide class="splide" :options=" {
                    type: 'loop', 
                    rewind: true, 
                    cover: false, 
                    autoplay: false, 
                    paginationKeyboard:false,
                    keyboard:false,
                    classes: {
                        arrow : 'splide__arrow arrow',
                        pagination: 'splide__pagination pagination',
                        page: 'splide__pagination__page pagination__item' },
                    breakpoints: {
                        475: {
                            arrows: false
                        },
                    }
                    }">
                    <SplideSlide>
                        <img src="@/assets/img/slider/slider-main-1.jpg" alt="">
                    </SplideSlide>
                    <SplideSlide>
                        <img src="@/assets/img/slider/slider-main-2.jpg" alt="">
                    </SplideSlide>
                    <SplideSlide>
                        <img src="@/assets/img/slider/slider-main-3.jpg" alt="">
                    </SplideSlide>
                </Splide>
            </section>
            </div>
        </div>
        <div class="container">
            <section class="home__products">
                <vue-element-loading :active="show" spinner="bar-fade-scale" :is-full-screen="true"/>
                <div class="home__products__item" v-for="(subcategory, index) in subcategories" :key="index">
                    <div class="section-head">
                        <h2>{{subcategory[0].title}}</h2>
                        <u><router-link :to="`/subcategory/${subcategory[0]._id}`">Посмотреть всё</router-link></u>
                    </div>
                    <div class="home__products__grid products-grid">
                        <product-item :items="subcategory[0].products"/>
                    </div>
                </div>
            </section>
        </div>
    </div>
</template>

<script>
import VueElementLoading from "vue-element-loading";
import productItem from '../components/product-item.vue'
import axios from 'axios'
import oneproductVue from "../components/oneproduct.vue";
export default {
    components:{
        productItem,
        VueElementLoading,
        oneproductVue
    },
    data(){
        return{
            show:true,
            subcategories:[],
            bottom: false,
            count:0,
            end: false
        }
    },
    watch: {
        bottom(newValue) {
            if (newValue) {
                this.addBeer();
            }
        },
        $route(){
            this.count = 0
            this.show = true
            document.title = 'Altech - Ноутбуки, Принтеры и Компьютерная Техника в Ташкенте!'
            this.addBeer()
        }
    },
    created() {
        window.addEventListener("scroll", () => {
            this.bottom = this.bottomVisible();
        });
        this.$store.dispatch('setSale')
        document.title = 'Altech - Ноутбуки, Принтеры и Компьютерная Техника в Ташкенте!'
        this.addBeer();
    },
    computed: {
        BASIC_URL(){
            return this.$store.getters.getURL
        },
        sale(){
            return this.$store.getters.getSale
        }
    },
    methods: {
        bottomVisible() {
            const scrollY = window.scrollY;
            const visible = document.documentElement.clientHeight;
            const pageHeight = document.documentElement.scrollHeight - 200;
            const bottomOfPage = visible + scrollY >= pageHeight;
            return bottomOfPage || pageHeight < visible;
        },
        addBeer() {
            this.show = true
            axios.get(`${this.BASIC_URL}/subcategory/api/forhome/${this.count}`).then((response) => {
                // console.log(response.data)
                if (response.data.length > 0 ) {
                    this.count++
                    this.subcategories.push(response.data)
                    this.show = false
                    if (this.bottomVisible()) {
                        this.addBeer();
                    }
                } else{
                    this.show = false
                }
            });
        }
    },

}

</script>

<style lang="less">
@import '../styles/home.less';
</style>